/**
 * @file 検索条件もっと見る
 *
 */
'use strict';

/**
 * @classname TableSearchConditionAccordionView
 * @classdesc 検索条件もっと見る
 */
export default class TableSearchConditionAccordionView {
	/**
	 * @constructor
	 * @param {jQuery} $e - 該当する jQuery オブジェクト
	 */
	constructor($e) {
		console.log('TableSearchConditionAccordionView.constructor');
		this._$e = $e;
		$e.on('click', () => this.onClick());

		//詳細な検索条件が指定されているが、openクラスが未設定の場合
		if(this._$e.attr('aria-expanded') == "1" && !this._$e.hasClass('open')){
			this._$e.click();
		}
	};

	/**
	 * クリックのコールバック
	 *
	 * @memberof TableSearchConditionAccordionView
	 * @return {undefined}
	 */
	onClick() {
		console.log('TableSearchConditionAccordionView.onClick');
		const $target = $('.block-accordion-target');
		if(this._$e.hasClass('open')){
			this._$e.removeClass('open').text('もっと検索条件を見る');
			$target.slideUp();
		}else{
			this._$e.addClass('open').text('検索条件を閉じる');
			$target.slideDown();
		}
	};
}
