/**
 * @file 採用詳細画面のcard-footerをfixedにする
 *
 */
'use strict';

/**
 * @classname FormFooterFixedView
 * @classdesc 採用詳細画面のcard-footerをfixedにする
 */
export default class FormFooterFixedView {
	/**
	 * @constructor
	 * @param {jQuery} $e - 該当する jQuery オブジェクト
	 */
	constructor($e) {
		console.log('FormFooterFixedView.constructor');
		this._$e = $e;

		$(document).ready( () => this.onResize() );
		$(window).resize( () => this.onResize() );
		$(window).scroll( () => this.onScroll() );
	};

	/**
	 * リサイズのコールバック
	 *
	 * @memberof FormFooterFixedView
	 * @return {undefined}
	 */
	onResize() {
		console.log('FormFooterFixedView.onResize');

		console.log(`before-${this._$e.width()}` );
		const $parent_width = this._$e.parent().width();
		this._$e.outerWidth(`${$parent_width}px`);
		console.log(`after-${this._$e.width()}` );

		this.onScroll();
	};

	/**
	 * スクロールのコールバック
	 *
	 * @memberof FormFooterFixedView
	 * @return {undefined}
	 */
	onScroll() {
		console.log('FormFooterFixedView.onScroll');
		const $scroll_value = $(window).scrollTop();
		console.log(`scroll:${$scroll_value}`);

		const $docHeight = $(document).innerHeight(); //ドキュメントの高さ
		const $windowHeight = $(window).innerHeight(); //ウィンドウの高さ
		const $pageBottom = $docHeight - $windowHeight; //ドキュメントの高さ - ウィンドウの高さ

		if($scroll_value > 50){
			this._$e.addClass("sticky");
			if($scroll_value >= $pageBottom){
				this._$e.removeClass("sticky");
			}else{
				this._$e.css('cssText', `display: block ; width:${this._$e.outerWidth()}px;`);
			}
		}else{
			this._$e.css('cssText', `display: none !important; width:${this._$e.outerWidth()}px;`);
		}
	};
}