/**
 * @file Form送信
 */
'use strict';
// utility class
import Common from '../../util/_common';


// 個別処理ファイルの読み込み
//import FormProduct from './form/_form-product';


/**
 * @classname FormSubmitView
 * @classdesc フォームサブミット
 */
export default class FormSubmitView {
	/**
	 * @constructor
	 * @param {jQuery} $e - 該当する jQuery オブジェクト
	 * @param {object} controller - コントローラー
	 */
	constructor($e, controller) {
		console.log('FormSubmitView.constructor');
		this._$e = $e;
		this._controller = controller;
		this._form = $e.attr('data-form');
		this._$form = $(this._form);
		this._process = $e.attr('data-process');
		this._timing = $e.attr('data-timing');
		this._submit = true;
		this._validate = false;
		if(this._process === 'true') {
			// 個別処理ファイルのアクティベート
			switch(this._form){
				case 'form-product':
					this._processing = new FormProduct(controller);
					break;
			}
		}

		this._$e.on('click', (e) => {
			e.preventDefault();
			if(!$e.hasClass('clicked')){
				$e.addClass('clicked');
				$e.append('<i class="fa fa-spinner fa-pulse ml-2">');
				this.onClick();
			}
		});
		this._controller.on('completePreProcessing', () => this.validate());
		this._controller.on('completeValidate', () => this.completeValidate());
		this._controller.on('completePostProcessing', () => this.addMinutes());
		this._controller.on('initialize', () => this.initialize());
	};


	/**
	 * クリックのコールバック
	 *
	 * @memberof FormSubmitView
	 * @return {undefined}
	 */
	onClick() {
		console.log('FormSubmitView.onClick');
		if(this._process === 'true'){
			if(this._timing === 'pre' || this._timing === 'both') {
				console.log('前処理実施');
				this._processing.preProcessing(this._$form);
			}else{
				console.log('前処理SKIP');
				this.validate(this._$form);
			}
		}else{
			console.log('前処理SKIP');
			this.validate(this._$form);
		}
	};


	/**
	 * バリデーションチェック
	 *
	 * @memberof FormSubmitView
	 * @return {undefined}
	 */
	validate() {
		if(this._validate) return;
		this._validate = true;
		console.log('FormSubmitView.validate');
		console.log('バリデーションチェック開始');
		this._controller.validate(this._$form);
	};


	/**
	 * バリデーション完了
	 *
	 * @memberof FormSubmitView
	 * @return {undefined}
	 */
	completeValidate() {
		console.log('FormSubmitView.completeValidate');
		console.log('バリデーションチェック完了');
		if(this._$form.find('.validate').hasClass('is-invalid')) this._submit = false;
		if(this._submit){
			console.log('エラーチェック：エラーなし');
			this.postProcessing();
		}else{
			$('[data-dismiss="modal"]').each((idx, e) => {
				$(e).click();
				// スクロールの速度
				var speed = 40; // ミリ秒
				// 移動先を取得
				var target = $('.error-tip').first();
				// 移動先を数値で取得
				var position = target.offset().top - 80;
				// スムーススクロール
				$('body,html').animate({scrollTop:position}, speed, 'linear');
			});
			Common.showErrorModal('入力項目に誤りがあります');
			this.initialize();
		}
	};


	/**
	 * 後処理
	 *
	 * @memberof FormSubmitView
	 * @return {undefined}
	 */
	postProcessing() {
		console.log('FormSubmitView.postProcessing');
		if(this._process === 'true'){
			if(this._timing === 'post' || this._timing === 'both') {
				console.log('後処理実施');
				this._processing.postProcessing(this._$form);
			}else{
				console.log('後処理SKIP');
				this.addMinutes();
			}
		}else{
			console.log('後処理SKIP');
			this.addMinutes();
		}
	};


	/**
	 * 分を追加する
	 *
	 * @memberof FormSubmitView
	 * @return {undefined}
	 */
	addMinutes() {
		console.log('FormSubmitView.addMinutes');
		$('input[data-time=true]').each(function(){
			let val = $(this).val();
			if(val.length === 16){
				val += ':00';
				console.log(val);
				$(this).val(val);
			}
		});
		this.submit();
	};


	/**
	 * submit
	 *
	 * @memberof FormSubmitView
	 * @return {undefined}
	 */
	submit() {
		console.log('FormSubmitView.submit');
		console.log('サブミット');
		$('.ignore-validate').removeAttr('name');
		this._$e.attr('disabled', 'disabled');
		setTimeout(() => {Common.submit(this._$form);},100);
	};


	/**
	 * 初期化
	 *
	 * @memberof FormSubmitView
	 * @return {undefined}
	 */
	initialize() {
		console.log('FormSubmitView.initialize');
		this._submit = true;
		this._validate = false;
		this._controller._$form = null;
		this._$e.removeClass('clicked').find('i').remove();
	};
}
