/**
 * @file フォームグループ削除
 */
'use strict';

/**
 * @classname FormDeleteFormGroupView
 * @classdesc フォームグループ削除
 */
export default class FormDeleteFormGroupView {
	/**
	 * @constructor
	 * @param {jQuery} $e - 該当する jQuery オブジェクト
	 */
	constructor($e) {
		console.log('FormDeleteFormGroupView.constructor');
		this._$e = $e;
		$e.on('click', () => this.onClick());
	}


	/**
	 * クリックのコールバック
	 *
	 * @memberof FormDeleteFormGroupView
	 * @return {undefined}
	 */
	onClick() {
		console.log('FormDeleteFormGroupView.onClick');
		this._$e.closest('.form-group').remove();
	};
}
