/**
 * @file Form Controller
 */

'use strict';
import Listener from '../util/_listener';
import ApplyClass from '../util/_apply-class';
import FormAddFormGroupView from '../view/form/_form-add-form-group-view';
import FormDeleteFormGroupView from '../view/form/_form-delete-form-group-view';
import FormValidateView from '../view/form/_form-validate-view';

let instance = null;

/**
 * @classname Form
 * @classdesc Form Controller
 */
export default class Form {

	/**
	 * @constructor
	 */
	constructor() {
		if (instance) {
			return instance;
		}
		console.log('Form.constructor');
		this._listener = new Listener();
		this._$form = null;
		this._submit = true;

		instance = this;
		return instance;
	};

	/**
	 * 前処理終了
	 *
	 * @memberof Form
	 * @param {jQuery} $form - フォームオブジェクト
	 * @return {undefined}
	 */
	completePreProcessing($form) {
		console.log('Form.completePreProcessing');
		this._$form = $form;
		this._listener.trigger('completePreProcessing');
	};


	/**
	 * バリデーションチェック
	 *
	 * @memberof Form
	 * @param {jQuery} $form - フォームオブジェクト
	 * @return {undefined}
	 */
	validate($form) {
		console.log('Form.validate');
		this._$form = $form;
		this._listener.trigger('validate');
		setTimeout(() => {
			this._listener.trigger('completeValidate');
		}, 500);
	};


	/**
	 * バリデーションチェック
	 *
	 * @memberof Form
	 * @param {jQuery} $form - フォームオブジェクト
	 * @return {undefined}
	 */
	validateModal($form) {
		console.log('Form.validate');
		this._$form = $form;
		this._listener.trigger('validate');
	};


	/**
	 * 後処理終了
	 *
	 * @memberof Form
	 * @param {jQuery} $form - フォームオブジェクト
	 * @return {undefined}
	 */
	completePostProcessing($form) {
		console.log('Form.completePostProcessing');
		this._$form = $form;
		this._listener.trigger('completePostProcessing');
	};


	/**
	 * サブミットボタン初期化
	 *
	 * @memberof Form
	 * @return {undefined}
	 */
	initialize() {
		console.log('Form.completePostProcessing');
		this._listener.trigger('initialize');
	};


	/**
	 * トリガーのセット
	 *
	 * @memberof Form
	 * @param {object} $formWrap - フォーム
	 * @return {undefined}
	 */
	setBtnTrigger($formWrap) {
		console.log('Form.setBtnTrigger');
		const $btnPlus = $formWrap.find('.form-group:last-child .btn-plus');
		const $btnMinus = $formWrap.find('.form-group:last-child .btn-minus');
		const $validate = $formWrap.find('.form-group:last-child .validate');
		ApplyClass.apply(FormAddFormGroupView, $btnPlus, [$btnPlus, this]);
		ApplyClass.apply(FormDeleteFormGroupView, $btnMinus, [$btnMinus]);
		ApplyClass.apply(FormValidateView, $validate, [$validate, this]);
	};


	/**
	 * listener on
	 *
	 * @memberof Form
	 * @param {string} name - イベント名
	 * @param {function} fn - 実行する関数
	 * @return {undefined}
	 */
	on(name, fn) {
		this._listener.on(name, fn);
	};


	/**
	 * listner off
	 *
	 * @memberof Form
	 * @param {string} name - イベント名
	 * @param {function} fn - 実行する関数
	 * @return {undefined}
	 */
	off(name, fn) {
		this._listener.off(name, fn);
	};
}
