/**
 * @file 検索デフォルト設定
 *
 */
'use strict';

/**
 * @classname TableSetDefaultSearchView
 * @classdesc 検索デフォルト設定
 */
export default class TableSetDefaultSearchView {
	/**
	 * @constructor
	 * @param {object} controller - controllerオブジェクト
	 * @param {jQuery} $e - 該当する jQuery オブジェクト
	 */
	constructor($e, controller) {
		console.log('TableSetDefaultSearchView.constructor');
		this._$e = $e;
		this._controller = controller;
		$e.on('click', () => this.onClick());
	};

	/**
	 * クリックのコールバック
	 *
	 * @memberof TableSetDefaultSearchView
	 * @return {undefined}
	 */
	onClick() {
		console.log('TableSetDefaultSearchView.onClick');

		this._controller.clearSearch();

		if( $('input[type=checkbox][data-search-item][data-search-clear-custom]').length ){
			$('input[type=checkbox][data-search-item][data-search-clear-custom]').prop('checked', false);
		}

		$('[data-search-item]').each((idx, e) => {
			const $e = $(e);
			if($e.attr('data-search-type') === 'checkbox'){
				if($e.attr('data-search-item') === 'status[]'){
					$('[data-set-default]').each((idx, e2) => {
						const $chkbox = $(e2);
						if( $e.val() == $chkbox.val() ) {
							$e.prop('checked', true);
						}
					});
				}
				if($e.attr('data-search-item') === 'facility[]'){
					$('[data-set-facility]').each((idx, e2) => {
						const $chkbox = $(e2);
						if( $e.val() == $chkbox.val() ) {
							$e.prop('checked', true);
						}
					});
				}
			}
		});

		$('.btn-table-search').click();

	};
}
