/**
 * @file コメントをAJAXで取得する
 *
 */
'use strict';

/**
 * @classname FormRegistClaimComments
 * @classdesc コメントをAJAXで取得する
 */
export default class FormRegistClaimComments {
	/**
	 * @constructor
	 * @param {object} controller - controllerオブジェクト
	 * @param {jQuery} $e - 該当する jQuery オブジェクト
	 */
	constructor($e, controller) {
		console.log('FormRegistClaimComments.constructor');
		this._$e = $e;
		this._tc_id = 0;
		this._controller = controller;
		this._request = null;
		this._$e.on('click', () => this.onClick());
	};

	/**
	 * クリックのコールバック
	 *
	 * @memberof FormRegistClaimComments
	 * @return {undefined}
	 */
	onClick() {
		console.log('FormRegistClaimComments.onClick');

		//コメントを登録
		const $modal_name = this._$e.attr('data-target');

		$.ajaxSetup({
			headers: {
				'X-CSRF-TOKEN': $('meta[name="csrf-token"]').attr('content')
			}
		});

		$('#claim-comment').submit(function(event) {

			$('#claim-comment').off();

			// HTMLでの送信をキャンセル
			event.preventDefault();
		 
			$('#modal-comment .modal-content #loading').show();
			// 操作対象のフォーム要素を取得
			const $form = $(this);

			//コメントを登録
			let _request = $.ajax({
				type: 'POST',
				data: $form.serialize(),
				url: location.href + '/../../comment/regist',
				cache: false,
				timeout: 3000
			});

			/* 成功時 */
			_request.done(function(data){
				$('button#reload').click();
				$('#modal-comment .modal-content #loading').hide();
			});

			/* 失敗時 */
			_request.fail(function(){
				$('#modal-comment .modal-content #loading').hide();
				alert("コメントの登録に失敗しました");
			});
		});
				
	};
}
