/**
 * @file 検索条件もっと見る
 *
 */
'use strict';

/**
 * @classname TablePaymentStateAccordionView
 * @classdesc 検索条件もっと見る
 */
export default class TablePaymentStateAccordionView {
	/**
	 * @constructor
	 * @param {jQuery} $e - 該当する jQuery オブジェクト
	 */
	constructor($e) {
		console.log('TablePaymentStateAccordionView.constructor');
		this._$e = $e;
		$e.on('click', () => this.onClick());
	};

	/**
	 * クリックのコールバック
	 *
	 * @memberof TablePaymentStateAccordionView
	 * @return {undefined}
	 */
	onClick() {
		console.log('TablePaymentStateAccordionView.onClick');
		const $target = $('.block-accordion-target');
		if(this._$e.hasClass('open')){
			this._$e.removeClass('open').text('詳細を表示する');
			$target.slideUp();
		}else{
			this._$e.addClass('open').text('詳細を隠す');
			$target.slideDown();
		}
	};
}
