/**
 * @file フォームグループ追加
 */
'use strict';
import Template from '../../template/_template';

/**
 * @classname FormAddFormGroupView
 * @classdesc フォームグループ追加
 */
export default class FormAddFormGroupView {
	/**
	 * @constructor
	 * @param {jQuery} $e - 該当する jQuery オブジェクト
	 * @param {object} controller - controllerオブジェクト
	 */
	constructor($e, controller) {
		console.log('FormAddFormGroupView.constructor');
		this._$e = $e;
		this._controller = controller;
		this._formType = $e.attr('data-form-type');
		this._$formWrap = this._$e.closest('.block-form-wrap');
		$e.on('click', () => this.onClick());
	}


	/**
	 * クリックのコールバック
	 *
	 * @memberof FormAddFormGroupView
	 * @return {undefined}
	 */
	onClick() {
		console.log('FormAddFormGroupView.onClick');
		let html = '';
		switch(this._formType){
			case 'facility_dept':
				html = Template.facilityDept();
				break;
		}
		this._$formWrap.append(html);
		this._controller.setBtnTrigger(this._$formWrap);
	};
}
