'use strict';

// utility class
import ApplyClass from '../util/_apply-class';

// form-class
import Form from '../controller/_form';
import FormValidateView from '../view/form/_form-validate-view';
import FormSubmitView from '../view/form/_form-submit-view';
import FormDatepickerView from '../view/form/_form-datepicker-view';
import FormAddFormGroupView from '../view/form/_form-add-form-group-view';
import FormSelectToggleView from '../view/form/_form-select-toggle-view';
import FormSelectActorIdView from '../view/form/_form-select-actor-id-view';
import FormFooterFixedView from '../view/form/_form-footer-fixed-view';
import FormSubmitView2 from '../view/form/_form-submit-view2';
import FormGetClaimComments from '../view/form/_form-get-claim-comments';
import FormRegistClaimComments from '../view/form/_form-regist-claim-comments';

$(function () {
	const FormController = new Form();
	// バリデート
	$('.validate').each((idx, e)=>{
		const $e = $(e);
		ApplyClass.apply(FormValidateView, $e, [$e, FormController]);
	});
	// フォームサブミット
	$('.btn[type=submit]').each((idx, e)=>{
		const $e = $(e);
		ApplyClass.apply(FormSubmitView, $e, [$e, FormController]);
	});
	// デートピッカー
	$('input[data-datepicker=true]').each((idx, e)=>{
		const $e = $(e);
		ApplyClass.apply(FormDatepickerView, $e, [$e, FormController]);
	});
	// 項目追加
	$('.btn-plus').each((idx, e)=>{
		const $e = $(e);
		ApplyClass.apply(FormAddFormGroupView, $e, [$e, FormController]);
	});
	// トグル（select）
	$('[data-select-toggle]').each((idx, e)=>{
		const $e = $(e);
		ApplyClass.apply(FormSelectToggleView, $e, [$e, FormController]);
	});
	// トグル（select）
	$('.actor_id').each((idx, e)=>{
		const $e = $(e);
		ApplyClass.apply(FormSelectActorIdView, $e, [$e, FormController]);
	});
	// 採用申請詳細のフッタ
	$('.apply_detail .card-footer').each((idx, e)=>{
		const $e = $(e);
		ApplyClass.apply(FormFooterFixedView, $e, [$e, FormController]);
	});
	// フォームサブミット
	$('.btn2[type=submit]').each((idx, e)=>{
		const $e = $(e);
		ApplyClass.apply(FormSubmitView2, $e, [$e, FormController]);
	});
	// 取引履歴コメント
	$('.comment').each((idx, e)=>{
		const $e = $(e);
		ApplyClass.apply(FormGetClaimComments, $e, [$e, FormController]);
	});
	// 取引履歴コメント登録
	$('.btn_comment').each((idx, e)=>{
		const $e = $(e);
		ApplyClass.apply(FormRegistClaimComments, $e, [$e, FormController]);
	});
});