/**
 * @file 検索条件もっと見る
 *
 */
'use strict';

/**
 * @classname TablePopOverApplyView
 * @classdesc 検索条件もっと見る
 */
export default class TablePopOverApplyView {
	/**
	 * @constructor
	 * @param {jQuery} $e - 該当する jQuery オブジェクト
	 */
	constructor($e) {
		console.log('TablePopOverApplyView.constructor');
		this._$e = $e;
		$e.on('mouseenter', () => this.onHover());
	};

	/**
	 * ホバーのコールバック
	 *
	 * @memberof TablePopOverApplyView
	 * @return {undefined}
	 */
	onHover() {
		console.log('TablePopOverApplyView.onHover');

		//ポップオーバーを複製
		const $popover = $(".popover-custom").clone();

		//ID付与
		$popover.attr('id', `apply_${this._$e.parent().index()}`);

		//ポップオーバーに値を設定
		let $row = $popover.find('.idx_1 .medicine_value p');//JANコード
		$row.text(this._$e.parent().children().eq(1).text());

		$row = $popover.find('.idx_2 .medicine_value p');//商品名
		$row.text(this._$e.parent().children().eq(3).text());

		$row = $popover.find('.idx_3 .medicine_value p');//一般名
		$row.text(this._$e.parent().children().eq(3).find('input:hidden[name="popular_name"]').val());

		$row = $popover.find('.idx_4 .medicine_value p');//包装薬価
		$row.text(this._$e.parent().children().eq(3).find('input:hidden[name="price"]').val());

		$row = $popover.find('.idx_5 .medicine_value p');//包装納入単価
		$row.text(this._$e.parent().children().eq(3).find('input:hidden[name="sales_price"]').val());

		$row = $popover.find('.idx_6 .medicine_value p');//12桁コード(医薬品CD)
		$row.text(this._$e.parent().children().eq(3).find('input:hidden[name="code"]').val());
		//詳細表示
		$row = $popover.find('a.btn');

		const $page = this._$e.parent().children().eq(15).find('input:hidden[name="page"]').val();
		const $fp_id = this._$e.parent().children().eq(15).find('input:hidden[name="fp_id"]').val();

		//申請がある場合
		if(this._$e.parent().children().eq(3).find('input:hidden[name="price_adoption_id"]').val() != 0){
			$row.attr( "href", `./apply/detail/${this._$e.parent().children().eq(3).find('input:hidden[name="price_adoption_id"]').val()}?flg=1&fp_id=${$fp_id}&page=${$page}` );
		}else{//ない場合
			$row.attr( "href", `./apply/detail/${this._$e.parent().children().eq(3).find('input:hidden[name="medicine_id"]').val()}?flg=0&fp_id=${$fp_id}&page=${$page}` );
		}

		//ホバー先に追加
		this._$e.append($popover);

		//ポップオーバーを表示
		const $td_p_top = parseInt(this._$e.css('padding-left'), 10);
		$popover.css('top', ($popover.outerHeight(true) - $td_p_top ) * -1);


		const $td_width = parseInt(this._$e.css('width'), 10);
		$popover.css('left', ( ($popover.outerWidth(true) - $td_width) / 2) * -1  );
		$popover.show();

		//削除
		$popover.on('click', (e) => {
			$(e.currentTarget).remove();
		});
		this._$e.on('mouseleave', () => {
			$popover.remove();
		});
				
	};
}