/**
 * @file Table Controller
 */

'use strict';
import Listener from '../util/_listener';
import Common from "../util/_common";

let instance = null;

/**
 * @classname Table
 * @classdesc Table Controller
 */
export default class Table {

	/**
	 * @constructor
	 */
	constructor() {
		if (instance) {
			return instance;
		}
		console.log('Table.constructor');
		this._listener = new Listener();

		this._error = false;


		instance = this;
		return instance;
	};

	/**
	 * 検索
	 *
	 * @memberof Table
	 * @return {undefined}
	 */
	search() {
		console.log('Table.search');
		this._error = false;
		let query = this._getSearchQuery();
		query = Table._setSort(query);
		this._setCount(query);
	};


	/**
	 * ソート
	 *
	 * @memberof Table
	 * @return {undefined}
	 */
	sort() {
		console.log('Table.sort');
		this._error = false;
		let query = this._getSearchQuery();
		query = Table._setSort(query);
		this._setCount(query);
	};


	/**
	 * ページネーション
	 *
	 * @memberof Table
	 * @param {int} page - ページ番号
	 * @return {undefined}
	 */
	pagination(page) {
		console.log('Table.pagination');
		this._error = false;
		let query = this._getSearchQuery();
		query = Table._setSort(query);
		if(page !== 1){
			if(query !== ''){
				query += `&page=${page}`;
			}else{
				query += `?page=${page}`;
			}
		}
		this._setCount(query);
	};


	/**
	 * 表示件数変更
	 *
	 * @memberof Table
	 * @param {integer} count - 件数
	 * @return {undefined}
	 */
	count(count) {
		console.log('Table.count');
		this._error = false;
		let query = this._getSearchQuery();
		query = Table._setSort(query);
		this._setCount(query, count);
	};


	/**
	 * 検索条件クリア
	 *
	 * @memberof Table
	 * @return {undefined}
	 */
	clearSearch() {
		console.log('Table.clearSearch');
		this._error = false;
		//let query = Table._setSort();
		//this._setCount(query);
		$('input[type=search][data-search-item]').val('');
		$('input[type=checkbox][data-search-item]').not('[data-search-clear-custom]').prop('checked', false);
		if( $('input[type=checkbox][data-search-item][data-search-clear-custom]').length ){
			if( !$('input[type=checkbox][data-search-item][data-search-clear-custom]').prop('checked') ){
				$('input[type=checkbox][data-search-item][data-search-clear-custom]').prop('checked', true);
			}
		}
		$('select[data-search-item]').val('');
	};


	/**
	 * 検索GETパラメーター取得
	 *
	 * @memberof Table
	 * @return {string} query - 検索GETパラメーター
	 */
	_getSearchQuery() {
		console.log('Table._getSearchQuery');
		let prefix = '?';
		let query = '';
		let radioName;
		$('[data-search-item]').each((idx, e) => {
			const $e = $(e);
			if($e.attr('data-search-type') === 'radio'){
				const name = $e.attr('name');
				if(radioName !== name){
					const value = $(`input[type=radio][name=${name}]:checked`).val();
					if(value !== 'all'){
						query += `${prefix}${$e.attr('data-search-item')}=${value}`;
						prefix = '&';
					}
					radioName = name;
				}
			}else if($e.attr('data-search-type') === 'checkbox'){
				if($e.prop('checked')) {
					const value = $e.val();
					query += `${prefix}${$e.attr('data-search-item')}=${value}`;
					prefix = '&';
				}
			}else{
				if($e.is(':invalid')){
					console.log($e.attr('data-search-item') + ': ' + $e.is(':valid'));
					Common.showErrorModal();
					this._error = true;
				}
				const value = encodeURIComponent($e.val());
				console.log(value);
				if(value !== ''){
					query += `${prefix}${$e.attr('data-search-item')}=${value}`;
					prefix = '&';
				}
			}
		});
		return query;
	};


	/**
	 * ソートパラメーターセット
	 *
	 * @memberof Table
	 * @param {string} query - 検索GETパラメーター
	 * @return {string} query - 検索GETパラメーター
	 */
	static _setSort(query = '') {
		console.log('Table._setSort');
		const $sortTable = $('.sort-table');
		const sortValue = $sortTable.val();
		const sortDefault = $sortTable.attr('data-sort-default');
		if(sortValue !== sortDefault){
			if(query !== ''){
				query += `&sort=${sortValue}`;
			}else{
				query += `?sort=${sortValue}`;
			}
		}
		return query;
	};


	/**
	 * カウントパラメーターセット
	 *
	 * @memberof Table
	 * @param {string} query - 検索GETパラメーター
	 * @param {number} count - 件数
	 * @return {string} query - 検索GETパラメーター
	 */
	_setCount(query = '', count) {
		console.log('Table._setCount');
		const cnt = count || parseInt($('.btn-search-count.active').attr('data-search-count'));
		if(cnt === 100 || cnt === 50 || cnt === 20){
			if(query !== ''){
				query = `${query}&page_count=${cnt}`;
			}else{
				query = `?page_count=${cnt}`;
			}
		}
		const url = Common.getCurrentURL();
		if(!this._error) location.href = `${url}${query}`;
	};


		/**
	 * listener on
	 *
	 * @memberof Table
	 * @param {string} name - イベント名
	 * @param {function} fn - 実行する関数
	 * @return {undefined}
	 */
	on(name, fn) {
		this._listener.on(name, fn);
	};


	/**
	 * listener off
	 *
	 * @memberof Table
	 * @param {string} name - イベント名
	 * @param {function} fn - 実行する関数
	 * @return {undefined}
	 */
	off(name, fn) {
		this._listener.off(name, fn);
	};
}
